body {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    width: 100%;
    background-color: var(--beige-color);
}

.mainHome {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0rem 1.5rem;
    margin-bottom: 7rem;
    width: 100%;
}

.mainHome h2 {
    text-align: center;
    font-family: var(--font-family);
    font-weight: var(--font-weight-medium);
    font-size: 0.75rem;
    color: var(--gray-color);
}

.article {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.container {
    display: flex;
    flex-direction: column;
    gap: 1.75rem;
    background-color: var(--dark-brown-color);
    border-radius: 20px;
    padding: 1rem 1.5rem;
}

.container h3 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 0.75rem;
    color: var(--white-color);
}

.container span {
    font-family: var(--font-family);
    font-weight: var(--font-weight-medium);
    font-size: 0.75rem;
    color: var(--white-color);
}

.info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30%;
    row-gap: 1rem;
}

.infoBlock {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.infoBlock p {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 0.75rem;
    color: var(--white-color);
}

.buttons {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.buttons button {
    background-color: var(--brown-color);
    border-radius: 20px;
    border: none;
    padding: 0.5rem;
    height: 2rem;
    width: 2rem;
}

.buttonsControl {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 5rem;
    background-color: rgb(251, 248, 239, 0.5);
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px 20px 0 0;
    padding: 0rem 1rem;
}

.addButton {
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 1.5rem;
    color: var(--white-color);
    background-color: var(--brown-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    height: 3rem;
    width: 3rem;
}

.analyticButton {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--brown-color);
    border-radius: 20px;
    border: none;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    height: 3rem;
    width: 3rem;
}

.changeProfile {
    font-family: var(--font-family);
    font-weight: var(--font-weight-medium);
    font-size: 0.75rem;
    color: var(--beige-color);
    background-color: var(--brown-color);
    border-radius: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    border: none;
    padding: 0.5rem 1rem;
}