* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --beige-color: #FBF8EF;
    --dark-brown-color: #493628;
    --brown-color: #7E634F;
    --gray-color: #4F4F4F;
    --white-color: #FFFFFF;
    --font-family: "Unbounded";
    --font-weight-black: 800;
    --font-weight-medium: 400;
}

header {
    margin-bottom: 3rem;
}

header h1 {
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 0.75rem;
    color: var(--gray-color);
}