* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
    --beige-color: #FBF8EF;
    --dark-brown-color: #493628;
    --brown-color: #7E634F;
    --gray-color: #4F4F4F;
    --white-color: #FFFFFF;
    --font-family: "Unbounded";
    --font-weight-black: 800;
    --font-weight-medium: 400;
}

.mainLogin {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0rem 1.5rem;
    width: 100%;
}

.mainLogin h2 {
    text-align: center;
    font-family: var(--font-family);
    font-weight: var(--font-weight-medium);
    font-size: 0.75rem;
    color: var(--gray-color);
}

.articleLogin {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    background-color: #292929;
    border-radius: 20px;
    padding: 3rem 4rem;
    height: 30rem;
    overflow: hidden;
    position: relative;
}

.articleLogin form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
}

.articleLogin button {
    font-family: var(--font-family);
    font-weight: var(--font-weight-black);
    font-size: 0.75rem;
    color: var(--beige-color);
    background-color: #292929;
    border-radius: 20px;
    border: 1px solid var(--beige-color);
    padding: 0.5rem;
}

.articleLogin img {
    position: absolute;
    bottom: -2px;
}

.inputForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.inputForm label {
    font-family: var(--font-family);
    font-weight: 200;
    font-size: 0.75rem;
    color: var(--beige-color);
}

.inputForm input {
    font-family: var(--font-family);
    font-weight: var(--font-weight-medium);
    font-size: 1rem;
    color: var(--gray-color);
    border-radius: 20px;
    border: none;
    width: 100%;
    padding: 0.3rem 0.5rem;
}